}

#next {
  position: absolute;
  opacity: 0.6;
  right: 23%;
  top: 91%;
  padding: 7px 10px 5px 10px;
  background-color: white ;
  border: 1px solid black ;
  border-radius: 5px;
  font-weight: bold;
  margin: 0px;
}
@media only screen and (max-width: 600px) {
  #next {   
    right: 7%;
    top: 85%;   
  }
}

#next:hover {
  opacity: 0.9;
}

