#grid-container { 
  display: grid; 
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); 
  width: 70%;
  margin: auto;
  grid-gap: 20px; 
  align-items: center;  
}

@media only screen and (max-width: 600px) {
  #grid-container { 
    display: block; 
    width: 100%;
    margin: auto;
  }
}

#grid-item {
  width: 250px;
  margin: auto;
  margin-bottom: 40px; 
}

@media only screen and (max-width: 600px) {
  #grid-item {
    width: 200px;
    margin: auto;
    margin-bottom: 40px; 
  }
}


