.dropbtn {
  display: none;
}

.dropdown-content a:hover {
  border-radius: 5px; 
  font-size: 23px;
}

.dropdown-content {
  font-size: 20px;
}

.dropdown-content a {
  color: black;
  width: 70%;
  padding: 5px 10px;
  text-decoration: none;
  display: block;
  margin: 10px auto;
}


@media only screen and (max-width: 600px) {
.dropbtn {
  display: block;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 65px;
  height: 45px;
  cursor: pointer;
  margin: 10px 20px;

}

.dropdown {
  position: relative;
  display: inline-block;
  background-color:rgb(71, 67, 67); 
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color:rgb(71, 67, 67); 
  border-radius: 10px;
  min-width: 100px;
  padding: 0px 10px 2px 10px; 
}

.dropdown-content a {
  width: 80%;
  color:white;
  /* padding: 2px 2px; */
  text-decoration: none;
  text-shadow: none;
  display: block;  
}

.dropdown-content a:hover {
  background-color: rgb(102, 102, 102);
  border: 1px solid white;
  font-size: 23px; 
}

.dropdown:hover .dropdown-content {
  display: block; 
}

.dropdown:hover .dropbtn {
  background-color: rgb(71, 67, 67);
}
}